import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment as env } from 'environments/environment';
import { PerfilResponse, Perfil } from '../models/perfil.model';
import { IFilter } from 'app/shared/models/interface.model';

@Injectable({
  providedIn: 'root',
})
export class PerfilService {
  readonly apipath = '/perfils';

  constructor(private http: HttpClient) {}

  getAll(filter: IFilter): Observable<PerfilResponse> {
    const parametros = new HttpParams()
      .set('filtro', filter.filtro)
      .set('page', filter.pagina)
      .set('linesPerPage', filter.tamanho)
      .set('orderBy', filter.ordenarPor)
      .set('direction', filter.direcaoOrdenacao);
    return this.http.get<PerfilResponse>(`${env.api}${this.apipath}/page`, {
      params: parametros,
    });
  }

  getById(id: number): Observable<Perfil> {
    return this.http.get<Perfil>(`${env.api}${this.apipath}/${id}`);
  }

  insert(perfil: Perfil): Observable<null> {
    return this.http.post<null>(`${env.api}${this.apipath}`, perfil);
  }

  update(perfil: Perfil): Observable<null> {
    return this.http.put<null>(
      `${env.api}${this.apipath}/${perfil.id}`,
      perfil
    );
  }

  delete(id: number): Observable<null> {
    return this.http.delete<null>(`${env.api}${this.apipath}/${id}`);
  }
}
