import { Component, OnInit } from '@angular/core';
import { MatPaginatorIntl, PageEvent } from '@angular/material/paginator';
import { FuseConfirmationService } from '@fuse/services/confirmation';
import { IFilter } from 'app/shared/models/interface.model';
import { PerfilService } from '../../api/perfil.service';
import { Perfil } from '../../models/perfil.model';
import toastr from 'toastr';

@Component({
  selector: 'app-perfil-list',
  templateUrl: './perfil-list.component.html',
  styleUrls: ['./perfil-list.component.scss'],
})
export class PerfilListComponent implements OnInit {
  perfils: Perfil[] = [];

  // MatPaginator Inputs
  length = 0;
  pageSize = 10;
  pageSizeOptions: number[] = [5, 10, 25, 100];

  // MatPaginator Output
  pageEvent: PageEvent;

  constructor(
    private _fuseConfirmationService: FuseConfirmationService,
    private _matPaginatorIntl: MatPaginatorIntl,
    private _perfilService: PerfilService
  ) {}

  ngOnInit(): void {
    this.carregaInformacoesPerfil();
    this._matPaginatorIntl.itemsPerPageLabel = 'Itens por pgina';
  }

  setPageSizeOptions(setPageSizeOptionsInput: string) {
    if (setPageSizeOptionsInput) {
      this.pageSizeOptions = setPageSizeOptionsInput
        .split(',')
        .map((str) => +str);
    }
  }

  carregaInformacoesPerfil(): void {
    const filtro: IFilter = {
      filtro: '',
      pagina: 0,
      direcaoOrdenacao: 'ASC',
      tamanho: 10,
      ordenarPor: 'id',
    };
    this._perfilService.getAll(filtro).subscribe((data) => {
      this.perfils = data.content;
      this.length = data.totalElements;
      this.pageSize = data.size;
    });
  }

  excluir(perfil: Perfil): void {
    const dialogRef = this._fuseConfirmationService.open({
      title: `Deseja realmente excluir o perfil ${perfil.nome}?`,
      message: '',
      icon: {
        show: true,
        name: 'heroicons_outline:exclamation',
        color: 'warn',
      },
      actions: {
        confirm: {
          show: true,
          label: 'Confirmar',
          color: 'warn',
        },
        cancel: {
          show: true,
          label: 'Cancelar',
        },
      },
      dismissible: false,
    });

    dialogRef.afterClosed().subscribe((result) => {
      if (result === 'confirmed') {
        this._perfilService.delete(perfil.id).subscribe(
          (res) => {
            toastr.success(`Perfil ${perfil.nome} excludo com sucesso!`, '', {
              timeOut: 10000,
              progressBar: true,
              closeButton: true,
            });
            this.carregaInformacoesPerfil();
          },
          (error) => {
            toastr.error(
              'No foi possvel excluir o perfil',
              error.error.error,
              {
                timeOut: 10000,
                progressBar: true,
                closeButton: true,
              }
            );
          }
        );
      }
    });
  }

  paginacao(event: Event): void {
    this.pageSize = event['pageSize'];
    const filtro: IFilter = {
      filtro: '',
      pagina: event['pageIndex'],
      direcaoOrdenacao: 'ASC',
      tamanho: this.pageSize,
      ordenarPor: 'id',
    };

    this._perfilService.getAll(filtro).subscribe((data) => {
      this.perfils = data.content;
      this.length = data.totalElements;
      this.pageSize = data.size;
    });
  }

  filtrarPerfils(termo: string): void {
    const filtro: IFilter = {
      filtro: termo,
      pagina: 0,
      direcaoOrdenacao: 'ASC',
      tamanho: 10,
      ordenarPor: 'id',
    };
    this._perfilService.getAll(filtro).subscribe((data) => {
      this.perfils = data.content;
      this.length = data.totalElements;
      this.pageSize = data.size;
    });
  }
}
