import { Component, OnInit } from '@angular/core';
import {
  UntypedFormBuilder,
  UntypedFormGroup,
  Validators,
} from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { PerfilService } from '../../api/perfil.service';
import { Perfil } from '../../models/perfil.model';
import toastr from 'toastr';

@Component({
  selector: 'app-perfil-form',
  templateUrl: './perfil-form.component.html',
  styleUrls: ['./perfil-form.component.scss'],
})
export class PerfilFormComponent implements OnInit {
  currentAction: string;
  form: UntypedFormGroup;
  pageTitle: string;
  perfil: Perfil;

  constructor(
    private _formBuilder: UntypedFormBuilder,
    private _route: ActivatedRoute,
    private _router: Router,
    private _perfilService: PerfilService
  ) {}

  ngOnInit(): void {
    this.setCurrentAction();
    this.criarFormularioPerfil();
    this.carregaPerfil();
  }

  ngAfterContentInit() {
    this.setPageTitle();
  }

  submitForm() {
    if (this.form.invalid) {
      return;
    }

    const perfil: Perfil = this.form.value;
    if (this.currentAction === 'novo') {
      this._perfilService.insert(perfil).subscribe(
        (response) => {
          toastr.success('Perfil cadastrado com sucesso!', 'Sucesso', {
              timeOut: 10000,
              progressBar: true,
              closeButton: true,
          })
          this._router.navigate(['perfil']);
        },
        (error) => {
          toastr.error('No foi possvel carregar o perfil', error.error.error, {
            timeOut: 10000,
            progressBar: true,
            closeButton: true,
          });
        }
      )
      return;
    }
    perfil.id = this.perfil.id;
    this._perfilService.update(perfil);

    this._router.navigate(['perfil']);
  }

  // PRIVATE METHODS

  private setCurrentAction() {
    if (this._route.snapshot.url[0]?.path === 'novo') {
      this.currentAction = 'novo';
    } else {
      this.currentAction = 'editar';
    }
  }

  private criarFormularioPerfil() {
    this.form = this._formBuilder.group({
      nome: [null, [Validators.required, Validators.minLength(2)]],
    });
  }

  private carregaPerfil() {
    if (this.currentAction === 'editar') {
      const idPerfil = Number(this._route.snapshot.url[0]?.path);
      this._perfilService.getById(idPerfil).subscribe(
        (perfil) => {
          this.perfil = perfil;
          this.form.patchValue(this.perfil);
        },
        (error) => {
          toastr.error('No foi possvel carregar o perfil', error.error.error, {
            timeOut: 10000,
            progressBar: true,
            closeButton: true,
          });
        }
      )

    }
  }

  private setPageTitle() {
    if (this.currentAction === 'novo') {
      this.pageTitle = 'Cadastro de Novo Perfil';
    } else {
      const perfilDescricao = this.perfil?.nome || '';
      this.pageTitle = 'Editando perfil: ' + perfilDescricao;
    }
  }
}
